<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name}">{{item.name_str}}</el-breadcrumb-item>
        </el-breadcrumb> 
      </div>
      <div class="createapply-group">
        <div class="viewapply-topinfobox">
          <div class="viewapply-topinfo-title">
            {{fixation.title}}
            <img v-if="mainStatus==2||mainStatus==3||mainStatus==4" class="viewapply-topinfo-resulticon" src="../../images/viewapply_successicon.png" alt="">
            <img v-if="mainStatus==-1" class="viewapply-topinfo-resulticon" src="../../images/viewapply_failicon.png" alt="">
            <img v-if="mainStatus==5" class="viewapply-topinfo-resulticon" src="../../images/viewapply_getbackicon.png" alt="">
          </div>
            <div class="viewapply-topinfo">
              <img class="viewapply-topinfo-comimg" :src="fixation.url" @error="getImgurl">
              <div class="viewapply-topinfo-textbox">
                <div class="viewapply-topinfo-text1">
                  <h2>申请人：{{fixation.name}}</h2>
                  <span>{{mainStatusStrList[mainStatus]}}</span>
                </div>
                <div class="viewapply-topinfo-text2">
                  审批编号：{{fixation.number}}
                </div>
              </div>
              <div class="viewapply-topinfo-btnbox">
                <span class="viewapply-topinfo-btn" v-for="item in click" @click="clickFunc(item)">{{clickList[item]}}</span>
              </div>
            </div>
          </div>
          <div class="clear table-group-title">
            <div class="breadcrumb" style="margin-top:20px">
            </div>
          </div>
          <div class="viewapply-content">
            <div class="viewapply-content-title viewapply-content-title-nobottom">
              {{fixation.groupName}}
            </div>
            <div class="viewapply-content-normal">
              <div class="viewapply-content-item">
                <div class="viewapply-content-item-title">提交时间</div>
                <div class="viewapply-content-item-content">{{fixation.add_time}}</div>
              </div>
              <div class="viewapply-content-item" v-if="type==1">
                <div class="viewapply-content-item-title">申请人所在部门</div>
                <div class="viewapply-content-item-content">{{fixation.depart_name}}</div>
              </div>
              <div class="viewapply-content-item" v-for="item in relax">
                <div class="viewapply-content-item-title">{{item.key}}</div>
                <div class="viewapply-content-item-content" v-if="!item.value">无</div>
                <div class="viewapply-content-item-content" v-else>{{item.value}}</div>
              </div>
            </div>
          </div>
          <div class="viewapply-content" v-if="file_list.length!=0">
            <div class="viewapply-content-title viewapply-content-title-nobottom">
              文件清单
            </div>
            <div class="viewapply-content-normal">
              <div class="viewapply-content-item">
                <div class="viewapply-content-item-title">文件：</div>
                <div class="viewapply-content-item-content">
                  <em v-if="file_list.length==0">无</em>
                  <div class="viewapply-content-item-imglist">
                    <div class="viewapply-content-item-imgItem" v-for="item in file_list">
                      <i class="filemanage-icon" :class="'icon-type-'+item.typestring"></i>
                      <div><a :href="item.url" target="_blank" download="下载">{{item.name}}</a></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="viewapply-content">
            <div class="viewapply-content-title">
              审批信息
            </div>
            <apply-approvebox-show-new :approve="approve" :discuss="totaldiscuss" @createCommentFun="createCommentFun($event)" @createreplyCommentFun="createreplyCommentFun($event)" @cancelReplyFun="cancelReplyFun($event)" @changeShowallFun="changeShowallFun($event)" @createDiscuss="createDiscuss($event)" @changediscussShowall="changediscussShowall" type="approve" :aType="type"></apply-approvebox-show-new>
          </div>
          <div class="viewapply-content" v-if="segment.length!=0">
            <div class="viewapply-content-title" v-if="type==2">
              用印环节
            </div>
            <div class="viewapply-content-title" v-if="type==3">
              签发环节
            </div>
            <apply-approvebox-show-new :approve="segment" type="segment" :aType="type"></apply-approvebox-show-new>
          </div>
          <div class="viewapply-content">
            <div class="viewapply-content-title">
              抄送人<em class="viewapply-content-title-nodata-span" v-if="copier.length==0">无</em>
            </div>
            <apply-copier :copier="copier"></apply-copier>
          </div>
        </div>
        <!-- 评论 -->
        <el-dialog title="评论" :visible.sync="commentDialogShow" width="559px" center :append-to-body='true' class="applyfun-modal">
          <div class="dialog_content">
            <div class="form-group-inputbox">
              <h2 class="form-group-inputbox-title">评论</h2>
              <div class="form-group-inputbox-selectbox select-normal-40 left">
                <textarea v-model="commentParam.content" placeholder="请输入评论内容" class="textarea-normal" name=""></textarea>
              </div>
            </div>
          </div>
          <div slot="footer" class="dialog_footer applyfun-modal-btnbox clear">
            <button class="dialog-btn-submit btn-blue" @click="submitComment">确定</button>
            <button class="dialog-btn-submit btn-blue-border" @click="commentDialogShow=false">取消</button>
          </div>
        </el-dialog>
        <!-- 审批 -->
        <el-dialog title="审批意见" :visible.sync="createDialogShow" width="559px" center :append-to-body='true' class="applyfun-modal" :before-close="dialogClose">
          <div class="dialog_content">
            <div class="form-group-inputbox">
              <h2 class="form-group-inputbox-title">审批意见</h2>
              <div class="form-group-inputbox-selectbox select-normal-40 left">
                <textarea v-model="approveSubmitParam.content" placeholder="请输入审批意见（选填）" class="textarea-normal" name=""></textarea>
              </div>
              <span class="form-group-inputbox-error"></span>
            </div>
            <div class="form-group-inputbox" v-if="button[0]!=undefined">
              <h2 class="form-group-inputbox-title">附件</h2>
              <div class="form-group-inputbox-fileselectbox">
                <input class="form-group-inputbox-fileselectbox-inputfile" type="file" multiple="multiple" @change="fileListChange($event)">
                <i class="el-icon-plus"></i>添加文件
              </div>
              <!-- <input class="form-group-inputbox-inputtext" type="text"> -->
              <span class="form-group-inputbox-error"></span>
              <div class="form-group-selectedfile-box">
                <div class="form-group-selectedfile-item clear" v-for="(item,index) in approveSubmitParam.fileList">
                  <span class="filemanage-icon icon-wd"></span>
                  <span class="form-group-selectedfile-filename">{{item.name}}</span>
                  <span class="el-icon-close" @click="deleteFile(index)"></span>
                </div>
              </div>
            </div>
          </div>
          <div slot="footer" class="dialog_footer applyfun-modal-btnbox clear">
            <button v-if="item.key!=-1&&item.key!=0" class="dialog-btn-submit btn-blue" v-for="item in button" @click="approveSubmit(item.key)">{{item.value}}</button>
            <button v-if="item.key==-1" class="dialog-btn-submit btn-blue-border" v-for="item in button" @click="approveSubmit(item.key)">{{item.value}}</button>
          </div>
        </el-dialog>
        <!-- 签字 -->
        <el-dialog title="确认意见并签字" :visible.sync="signDialogShow" width="500px" center :append-to-body='true' class="applyfun-modal" :before-close="signDialogShowClose()">
          <div class="dialog_content">
            <div class="form-group-inputbox flex">
              <h2 class="form-group-inputbox-title">在此签字：</h2>
              <div class="form-group-inputbox-signbox select-normal-40 left" ref="canvasHW">
                <canvas @mousedown="mouseDown" @mousemove="mouseMove" @mouseup="mouseUp" ref="canvasF"></canvas>
              </div>
            </div>
          </div>
          <div slot="footer" class="dialog_footer applyfun-modal-btnbox clear">
            <button v-for="item in button" class="dialog-btn-submit btn-blue" @click="signSubmit(item.key)">{{item.value}}</button>
          </div>
        </el-dialog>
      </div>
    </div>
</template>
<script>
//审批流程状态
import applyApproveboxShowNew from './applyApproveboxShowNew.vue' //顶部流程模块
import applyCopier from '../apply/applyCopier.vue' //抄送人
import { spResult, submitComment, showApproveNew, spUrgent, spCancel, deriveWps, spOver, applyReply, newapplyUploadFile, createDicussapi, newDeleteFileUrl, signResult, leaveResult } from '../../config/api'
import vueImages from 'vue-images'
import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
export default {
  data() {
    return {
      routeList: [],
      approve: '', //审批信息
      discuss: '', //总评
      totaldiscuss: {
        content: '',
        discusslist: '',
        show: false,
        showall: false,
      },
      segment: [], //用印信息
      fixation: '', //顶部信息
      type: 1, //审批类型,
      template_id:'',//审批模版
      relax: '', //基础信息
      file_list: '', //文件
      approval_id: 1,
      mainStatus: 1, //
      mainStatusStr: '', //
      is_mine: '', //1=>发起人 2=>审批人
      click: '',
      button: '', //按钮
      copier: '',
      mainStatusStrList: {
        '-1': '审批不通过',
        0: '待审批',
        1: '审批中',
        2: '审批通过',
        3: '待用印',
        4: '审批结束',
        5: '已撤销'
      },
      clickList: {
        1: '申请加急',
        2: '修改审批',
        3: '重新发起', //===>暂无
        4: '撤回审批',
        5: '审批',
        6: '导出审批单',
        7: '审批完成',
        8: '确认意见并签字',
        9: '文件签发', //===>暂无
        10: '评论'
      },
      ///////////////////////////评论弹窗
      commentDialogShow: false,
      commentParam: {
        content: ''
      },
      ///////////////////////////审批弹窗
      createDialogShow: false,
      approveSubmitParam: {
        content: '',
        fileList: []
      },
      ////////////////签字弹窗
      signDialogShow: false,
      signDialogType: 1, ////1=> 确认意见并签字 2=>文件签发
      points: [],
      canvasTxt: null,
      startX: 0,
      startY: 0,
      moveY: 0,
      moveX: 0,
      endY: 0,
      endX: 0,
      w: null,
      h: null,
      isDown: false
    }
  },
  created() {
    this.routeList = this.$route.meta.routeList;
    this.routeList.forEach(element => {
      if (element.name_str == '行政管理') {
        element.name_str = this.$route.query.aName;
      }
    });
    if (!this.$route.query.from) {
      this.pageType = '';
      this.$router.push({
        name: 'investProjectManageList'
      })
    } else {
      this.pageType = this.$route.query.from
    }
    if (!this.$route.query.approval_id || !this.$route.query.status) {
      if (this.pageType == 2) {
        this.$router.push({
          name: 'investProjectManageList'
        })
      } else {
        this.$router.push({
          name: 'administrativeManageHomepage'
        })
      }
    } else {
      this.approval_id = this.$route.query.approval_id;
      this.is_mine = this.$route.query.status == 2 ? 0 : this.$route.query.status;
      this.initData();
    }
    this.initData();
  },
  components: {
    vueImages,
    applyApproveboxShowNew,
    applyCopier
  },
  computed: {},
  mounted() {},
  methods: {
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.realList = to.meta.routerList
      });
    },
    initData() {
      showApproveNew({
        aid: this.approval_id,
        type: '',
        is_mine: this.is_mine
      }).then(res => {
        if (res.data.msgNo == 1) {
          let approvelist = res.data.payload.flow;
          approvelist.forEach(item => {
            item.showOpinion = true;
            item.showComment = true;
            if (item.status == 0 || item.content == null || item.comment == undefined) {
              item.showOpinion = false;
            } else {
              if (item.content == '') {
                if (item.attach == undefined || item.attach.length == 0) {
                  item.showOpinion = false;
                } else {
                  item.showOpinion = true;
                }
              } else {
                item.showOpinion = true;
              }
              if (item.comment.length == 0) {
                item.showComment = false
              }
            };
            if (item.comment != undefined) {
              if (item.comment.length > 2) {
                item.showall = true;
              } else {
                item.showall = false;
              }
            }
            if (item.attach == undefined) {
              item.attach = [];
            } else {
              this.getFileType(item.attach)
            }
            item.newComment = {
              id: item.id,
              comment_id: 0,
              content: '',
              placeholder: '写下你的评论'
            };
          })
          this.approve = approvelist;
          if (res.data.payload.handle) {
            this.segment = res.data.payload.handle;
          }
          this.relax = res.data.payload.relax;
          this.click = res.data.payload.click;
          if (this.click.indexOf(10) != -1) {
            this.totaldiscuss.show = true;
          }
          if (res.data.payload.discuss != undefined) {
            this.totaldiscuss.discusslist = res.data.payload.discuss;
            if (this.totaldiscuss.discusslist.length > 2) {
              this.totaldiscuss.showall = true;
            } else {
              this.totaldiscuss.showall = false;
            }
          } else {
            this.totaldiscuss.discusslist = [];
          }
          this.file_list = res.data.payload.files;
          this.getFileType(this.file_list);
          this.fixation = res.data.payload.fixation;
          this.type = this.fixation.group;
          this.template_id = this.fixation.tid;
          this.mainStatus = this.fixation.mainStatus;
          this.button = res.data.payload.button;
          this.copier = res.data.payload.copier;
        }
      })
    },
    //操作按钮
    clickFunc(click) {
      switch (click) {
        case 1:
          this.applyUrgentFun();
          break;
        case 2:
          this.gotoEditapply(1);
          break;
        case 3:
          this.gotoEditapply(2);
          break;
        case 4:
          this.cancelApproveFun();
          break;
        case 5:
          this.approveFun();
          break;
        case 6:
          this.derivePdfFun();
          break;
        case 7:
          this.finishApproveFun();
          break;
        case 8:
          this.initSignModal(1);
          break;
        case 9:
          this.initSignModal(2);
          break;
        case 10:
          this.createComment(2);
          break;
      }
    },
    //click事件
    //1=>申请加急
    applyUrgentFun() {
      spUrgent({
        aid: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', res.data.payload)
        } else {
          dialogConfirm('fail', res.data.message)
        }
        this.initData();
      })
    },
    //2=>修改审批//3=>重发审批
    gotoEditapply(type) {
      this.$router.push({
        query: {
          approval_id: this.approval_id,
          type: type, //1=》编辑，2=〉重新发起
          aType: this.type,
          template_id:this.template_id,
          from: this.pageType
        },
        name: 'updateApplyNew'
      })
    },
    //4=>撤回审批
    cancelApproveFun() {
      spCancel({
        aid: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '提交成功！');
          this.signDialogShow = false;
        } else {
          dialogConfirm('fail', res.data.message)
        }
        this.initData();
      })
    },
    //5=>审批
    approveFun() {
      this.createDialogShow = true;
      this.approveSubmitParam = {
        content: '',
        fileList: []
      }
    },
    //6=>导出审批单
    derivePdfFun() {
      deriveWps({
        aid: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          window.open(res.data.payload.url)
        } else {
          dialogConfirm('fail', res.data.message)
        }
      })
    },
    //7=>审批完成
    finishApproveFun() {
      spOver({
        aid: this.approval_id
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', res.data.message);
        } else {
          dialogConfirm('info', res.data.message);
        }
        this.initData();
      })
    },
    //8=>确认意见并签字//9=>文件签发
    initSignModal(type) {
      let that = this;
      that.signDialogShow = true;
      that.signDialogType = type;
      this.canvasTxt = null;
      let timer = setInterval(function() {
        if (that.$refs.canvasF != undefined) {
          let canvas = that.$refs.canvasF;
          canvas.height = that.$refs.canvasHW.offsetHeight;
          canvas.width = that.$refs.canvasHW.offsetWidth;
          that.canvasTxt = canvas.getContext("2d");
          that.canvasTxt.fillStyle = "white";
          that.canvasTxt.fillRect(0, 0, that.$refs.canvasHW.offsetHeight, that.$refs.canvasHW.offsetWidth);
          
          clearInterval(timer);
        }
      }, 500)
      
    },
    signDialogShowClose(){
      if (this.$refs.canvasF != undefined) {
          let canvas = this.$refs.canvasF;
          canvas.height = this.$refs.canvasHW.offsetHeight;
          canvas.width = this.$refs.canvasHW.offsetWidth;
          this.canvasTxt = canvas.getContext("2d");
          this.canvasTxt.fillStyle = "white";
          this.canvasTxt.fillRect(0, 0, this.$refs.canvasHW.offsetHeight, this.$refs.canvasHW.offsetWidth);
      }
    },
    //10=>评论
    createComment() {
      this.commentDialogShow = true;
      this.commentParam.content = '';
    },
    //提交评论
    submitComment() {
      if (this.commentParam.content == '') {
        dialogConfirm('info', '评论不能为空');
        return;
      }
      submitComment({
        aid: this.approval_id,
        content: this.commentParam.content
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '评论成功');
          this.commentDialogShow = false;
        } else {
          dialogConfirm('fail', res.data.message)
        }
        this.initData();
      })
    },
    //审批提交
    approveSubmit(type) {
      let param = {
        aid: this.approval_id,
        type: type,
        content: this.approveSubmitParam.content
      }
      if (this.approveSubmitParam.fileList.length != 0) {
        param.file = JSON.stringify(this.approveSubmitParam.fileList)
      }
      spResult(param).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '审批成功！')
          this.createDialogShow = false;
          this.initData();
        } else {
          dialogConfirm('fail', res.data.message);
        }
      })
    },
    //评论
    createCommentFun(e) {
      this.approve[e.cindex].newComment.content = '';
      applyReply(e.param).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '评论成功');
          this.initData();
        } else {
          dialogConfirm('fail', res.data.message)
        }
      })
    },
    //回复评论
    createreplyCommentFun(e) {
      this.approve[e.cindex].newComment.content = '';
      this.approve[e.cindex].newComment.comment_id = e.comment_id;
      this.approve[e.cindex].newComment.placeholder = '回复' + e.replier;
    },
    //取消回复
    cancelReplyFun(e) {
      this.approve[e.cindex].newComment.content = '';
      this.approve[e.cindex].newComment.comment_id = 0;
      this.approve[e.cindex].newComment.placeholder = '写下你的评论';
    },
    //展开评论
    changeShowallFun(e) {
      this.approve[e.cindex].showall = !this.approve[e.cindex].showall;
    },
    //展开评论
    changediscussShowall(e) {
      this.totaldiscuss.showall = !this.totaldiscuss.showall;
    },
    //选择文件
    fileListChange(e) {
      let file = [];
      for (let i = 0; i < e.target.files.length; i++) {
        file.push(e.target.files[i]);
      }
      newapplyUploadFile(file).then(res => {
        if (res.data.msgNo == 1) {
          this.approveSubmitParam.fileList.push(res.data.payload);
        } else {
          dialogConfirm('fail', res.data.message);
        }
        e.target.value = "";
      });
    },
    //删除文件
    deleteFile(index) {
      newDeleteFileUrl({
        path: encodeURIComponent(this.approveSubmitParam.fileList[index].url)
      }).then(res => {})
      this.approveSubmitParam.fileList.splice(index, 1);
    },
    //关闭审批弹窗
    dialogClose() {
      this.deletefileFun();
      this.createDialogShow = false;
    },
    //批量删除文件
    deletefileFun() {
      this.approveSubmitParam.fileList.forEach(item => {
        newDeleteFileUrl({
          path: encodeURIComponent(item.url)
        }).then(res => {})
      })
      this.delFilelist = [];
    },

    //判断文件名是否符合
    fileListChangeGetFileType(filename) {
      let type = filename.substring(filename.lastIndexOf("."), filename.length);
      let unsupportType = ['.3gpp', '.ac3', '.mp2', '.mp3', '.mp4', '.mpeg', '.mpg'];
      if (unsupportType.indexOf(type) == -1) {
        return true
      } else {
        return false
      }
    },
    //获取文件类型
    getFileType(fileList) {
      let typelist = ['pdf', 'txt', 'ppt', 'xlsx', 'doc', 'docx', 'pic', 'zip', 'txt', 'rar', 'mp3'];
      let piclist = ['png', 'gif', 'bmp', 'tif', 'tiff', 'jpe', 'jpeg', 'jpg'];
      let pptlist = ['ppt', 'pptx'];
      let doclist = ['doc', 'docx'];
      let xlslist = ['xls', 'xlsx'];
      let ziplist = ['zip', 'rar'];
      let movlist = ['wmv', 'mp4'];
      let txtlist = ['txt', 'xml', 'html', 'css', 'js'];
      let mp3list = ['mp3', 'mp2', 'mpe', 'mpeg', 'mpg'];
      fileList.forEach(item => {
        let type = this.getFileTypeString(item.name);
        if (typelist.indexOf(type) != -1) {
          item.typestring = type;
        } else if (piclist.indexOf(type) != -1) {
          item.typestring = 'pic';
        } else if (pptlist.indexOf(type) != -1) {
          item.typestring = 'ppt';
        } else if (doclist.indexOf(type) != -1) {
          item.typestring = 'doc';
        } else if (xlslist.indexOf(type) != -1) {
          item.typestring = 'xls';
        } else if (txtlist.indexOf(type) != -1) {
          item.typestring = 'txt';
        } else if (mp3list.indexOf(type) != -1) {
          item.typestring = 'mp3';
        } else if (ziplist.indexOf(type) != -1) {
          item.typestring = 'rar';
        } else if (movlist.indexOf(type) != -1) {
          item.typestring = 'mov';
        } else {
          item.typestring = 'unno';
        }
      })
    },
    //截取文件后缀名
    getFileTypeString(filename) {
      filename = filename.toString();
      let type = filename.substring(filename.lastIndexOf(".") + 1, filename.length);
      return type;
    },
    //获取图片
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    },
    /////////////////////////////////////////签字
    mouseDown(ev) {
      if (this.canvasTxt != null) {
        ev = ev || event;
        ev.preventDefault();
        if (1) {
          let obj = {
            x: ev.offsetX,
            y: ev.offsetY
          };
          this.startX = obj.x;
          this.startY = obj.y;
          this.canvasTxt.beginPath();
          this.canvasTxt.moveTo(this.startX, this.startY);
          this.canvasTxt.lineTo(obj.x, obj.y);
          this.canvasTxt.stroke();
          this.canvasTxt.closePath();
          this.points.push(obj);
          this.isDown = true;
        }
      }
    },
    mouseMove(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (this.isDown) {
        let obj = {
          x: ev.offsetX,
          y: ev.offsetY
        };
        this.moveY = obj.y;
        this.moveX = obj.x;
        this.canvasTxt.beginPath();
        this.canvasTxt.moveTo(this.startX, this.startY);
        this.canvasTxt.lineTo(obj.x, obj.y);
        this.canvasTxt.stroke();
        this.canvasTxt.closePath();
        this.startY = obj.y;
        this.startX = obj.x;
        this.points.push(obj);
      }
    },
    mouseUp(ev) {
      ev = ev || event;
      ev.preventDefault();
      if (1) {
        let obj = {
          x: ev.offsetX,
          y: ev.offsetY
        };
        this.canvasTxt.beginPath();
        this.canvasTxt.moveTo(this.startX, this.startY);
        this.canvasTxt.lineTo(obj.x, obj.y);
        this.canvasTxt.stroke();
        this.canvasTxt.closePath();
        this.points.push(obj);
        this.points.push({ x: -1, y: -1 });
        this.isDown = false;
      }
    },
    signSubmit(key) {
      let canvas = this.$refs.canvasF;
      let imgBase = canvas.toDataURL("image/jpeg", 0.6);
      //let file = '';
      spResult({
        aid: this.approval_id,
        type: key,
        file: imgBase
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '提交成功！')
          this.signDialogShow = false;
          this.initData();
        } else {
          dialogConfirm('fail', res.data.message)
        }
      })
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal';
@import '../../style/apply';

.form-group-inputbox-fileselectbox {
  margin-bottom: 10px;
}

.applyfun-modal .textarea-normal {
  height: 100px;
}

.applyfun-modal .filemanage-icon {
  background: url('../../images/filemanage_icon.png') no-repeat;
  background-position: 0px -240px;
}

.applyfun-modal-btnbox {

  //margin-top: 15px;
  .dialog-btn-submit {
    margin-left: 20px;
  }
}

.form-group-inputbox-signbox {
  width: 302px;
  height: 302px;
  border: 1px solid #eee;
  canvas{
    width: 100% !important;
    height: 100% !important;
  }
}

</style>



